import Layout from '@/views/layout/layer'

/**/
const button= ()=> import("@/views/components/button")
/**/
const loading= ()=> import("@/views/components/loading")
/**/
const message= ()=> import("@/views/components/message")
/**/
const tooltip= ()=> import("@/views/components/tooltip")
/**/
const Progress= ()=> import("@/views/components/Progress")
/*&*/
const PullToRefresh= ()=> import("@/views/components/PullToRefresh")

export default {
  path: '/components',
  name: '',
  icon:'zujianku',
  component: Layout,
  redirect:"/components/button",
  children:[
  	{
	  	path:"button",
	  	name:"",
      component: button,  
  	},   
    {
      path:"Progress",
      name:"",
      component: Progress,  
    },   
    {
      path:"loading",
      name:"",
      component: loading,  
    },    
    {
      path:"message",
      name:"",
      component: message,  
    },     
    {
      path:"tooltip",
      name:"",
      component: tooltip,  
    },     
    {
      path:"pullRefresh",
      name:"&",
      component: PullToRefresh,  
    }
  ]
}


