import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

/**/
import login from '@/views/login/login'

/**/
import Layout from '@/views/layout/layer'

/**/
import sketch from '@/views/sketch'



/**/
import components from './components'
/*demo*/
import demos from './demo'


/**/
import locking from '@/views/locking/locking'
/*404*/
import nopage from '@/views/404/404'

/*
hidden:
noDropdown:   default:false
*/
export default new Router({
  //mode: 'history',
  linkActiveClass:"isActive",
  routes: [
    {
      path: '/login',
      name: '',
      hidden:"true",
      component: login
    },
    {
      path: '/',
      name: '',
      hidden:"true",
      component: Layout,
      redirect:"introduction",
    },
    {
      path:"/introduction",
      component: Layout,
      icon:"jianshu",
      name:"",
      noDropdown:true,
      redirect:"/introduction/index",
      children:[     
        {       
          component: sketch,        
          path:"index",
        }
      ]
    },
    components,
    demos,
    { path: '/locking',
      name:"",
      icon:"lock",
      component: locking
    },    
    { path: '/404',
      name:"404",
      hidden: true,
      component: nopage
    },
    { path: '*',
      redirect:"/404",
      hidden: true,
    }
  ]
})



